img.project__img {
  border-radius: 10px;
  width: 100%;
  object-fit: cover;
  transition: 0.4s;
  opacity: 0.7;
  position: relative;
}
img.project__img::after {
  content: "";
  width: 100%;
  height: 100%;
  background-color: #000;
  z-index: 1;
  opacity: 1;
}
.legend__container {
  width: 70%;
  margin: 30px auto;
}
.mask__effect {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #2fa463;
  border-radius: 10px;
  transition: 0.4s;
  opacity: 0.3;
  display: none;
}
.project__box:hover .mask__effect {
  display: inline;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.project__box:hover img.project__img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.project__meta.absolute {
