  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  height: auto;
  width: 100%;
  object-fit: cover;
  object-position: bottom;
}

.swiper-button-prev {
  background-color: #ffffffae;
